export default class DropZone extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        className: 'drop-zone-hide'
      }
      this._onDragEnter = this._onDragEnter.bind(this);
      this._onDragLeave = this._onDragLeave.bind(this);
      this._onDragOver = this._onDragOver.bind(this);
      this._onDrop = this._onDrop.bind(this);
    }
    
    componentDidMount() {
      window.addEventListener(this.props.name+'mouseup', this._onDragLeave);
      window.addEventListener(this.props.name+'dragenter', this._onDragEnter);
      window.addEventListener(this.props.name+'dragover', this._onDragOver);
      document.getElementById(this.props.name+"dragbox").addEventListener(this.props.name+'dragleave', this._onDragLeave);
      window.addEventListener(this.props.name+'drop', this._onDrop);
    }
    
    componentWillUnmount() {
      window.removeEventListener(this.props.name+'mouseup', this._onDragLeave);
      window.removeEventListener(this.props.name+'dragenter', this._onDragEnter);
      window.addEventListener(this.props.name+'dragover', this._onDragOver);
      document.getElementById(this.props.name+"dragbox").removeEventListener(this.props.name+'dragleave', this._onDragLeave);
      window.removeEventListener(this.props.name+'drop', this._onDrop);
    }
    
    _onDragEnter(e) {
      this.setState({ className: 'drop-zone-show' });
      e.stopPropagation();
      e.preventDefault();
      return false;
    }
    
    _onDragOver(e) {
      e.preventDefault();
      e.stopPropagation();
      return false;
    }
    
    _onDragLeave(e) {
      this.setState({className: 'drop-zone-hide'});
      e.stopPropagation();
      e.preventDefault();
      return false;
    }
    
    _onDrop(e) {
      e.preventDefault();
      let files = e.dataTransfer.files;
      console.log(this.props.name+' said: ', files);
      this.setState({className: 'drop-zone-hide'});
      return false;
    }
    
    render() {
      return (
        <div>
          {this.props.children}
          <div id={this.props.name+"dragbox"} className={this.state.className}>
            Drop a file to Upload
          </div>
        </div>
      );
    }
  }